
#product-page{
	@extend .dt-page-wrapper;

	.product-subtitle{
		border-bottom: $border-width solid $brand-primary;
		margin-bottom: $spacer;

		.product-meta{
			display: inline-block;

			+ .product-meta::before{
				content: "|";
				margin: 0 $spacer;

			}
		}
	}

	.rating-product{
		display: inline;
	}

	.product-cover{
		border: $border-width solid $gray-light;

		.img{
			width: 100%;
			height: auto;
		}
	}

	.owl-carousel{
		margin-top: $spacer/2;

		.owl-nav{
			font-size: 0;

			.owl-prev{
				position: absolute;
				top: 40px;
				background-color: $brand-primary;
				color: $gray-light;
				left: -10px;
			}

			.owl-next{
				position: absolute;
				top: 40px;
				background-color: $brand-primary;
				color: $gray-light;
				right: -10px;
			}
		}
				
		.item{
			border: $border-width solid $gray-light;
		}
	}

	.product-flags{
		.new{
			@include flag-new;
		}

		.on-sale{
			@include flag-on-sale;
		}

		.online-only {
			@include flag-online-only;
		}

		.pack{
			@include flag-pack;
		}
	}

	.regular-price{
		color: $brand-danger;
		text-decoration: line-through;
		font-size: $font-size-lg;
		font-weight: 200;
	}

	.discount-percentage{
		background-color: $brand-info;
		border: $border-width solid $brand-success;
		padding: 0 $spacer/2;
		color: $gray-lighter;
		margin-left: $spacer;
	}

	.price{
		font-size: $font-size-lg*2;
		line-height: 1;
	}

	.input-color{
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 35px;
		width: 35px;

		&:checked + span{
			border: $border-width solid $black;
		}
		
	}

	.color{
		font-size: 0;
		overflow: hidden;
		width: 35px;
		height: 35px;
		background-size: contain;
		display: inline-block;
	}

	.bootstrap-touchspin{

		width: 20%;

		.touchspin-plus{
			background-image: url('../img/plus.svg');
			background-size: 50%;
			background-position: 50%;
			background-repeat: no-repeat;
			padding: $spacer/2;
		}

		.touchspin-minus{
			background-image: url('../img/minus.svg');
			background-size: 50%;
			background-position: 50%;
			background-repeat: no-repeat;
			padding: $spacer/2;
		}

		.input-group-btn-vertical{
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;
			margin-left: -2px;
		}

		.form-control{
			background: none;
			height: inherit;
		}
	}

	.tab-pane{
		padding: $spacer;
		background-color: $white;
	}

	.nav-item{
		margin-bottom: -0.125rem;
	}

	.nav-link{
		background-color: transparent;
		border: none;
		color: $gray;
		text-transform: uppercase;
		font-family: "Montserrat";

		&.active{
			color: $brand-primary;
			border-bottom: $border-width+1 solid $brand-primary;
		}

		&:hover{
			color: $brand-primary;
		}
	}

	.nav-tabs{
		border-bottom: $border-width solid $gray-light;
	}

}


